import { Component, ChangeDetectionStrategy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, zip } from 'rxjs';
import { LocalStorageService } from 'angular-2-local-storage';

import { FADE, SLIDE } from '@bp/shared';
import { flatMap } from 'rxjs/operators';
import { isEqual } from 'lodash-es';
import { Dictionary } from 'lodash';
import { Router } from '@angular/router';

@Component({
	selector: 'bp-contact-form',
	templateUrl: './contact-form.component.html',
	styleUrls: ['./contact-form.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	animations: [FADE, SLIDE]
})
export class ContactFormComponent {
	form = new FormGroup(<Typify<IFeedbackForm, FormControl>>{
		fullName: new FormControl('', Validators.required),
		email: new FormControl('', [Validators.required, Validators.email]),
		phone: new FormControl(''),
	});

	submitting$ = new BehaviorSubject(false);
	error$ = new BehaviorSubject(false);

	httpOptions = {
		headers: new HttpHeaders({
			'Content-Type': 'application/x-www-form-urlencoded'
		})
	};

	constructor(
		private http: HttpClient,
		private localStorage: LocalStorageService,
		private router: Router
	) { }

	submit() {
		if (this.form.invalid || this.submitting$.value)
			return;

		if (isEqual(this.localStorage.get('[promo].contact-data'), this.form.value)) {
			this.router.navigate(['/gratitude']);
			return;
		}

		this.localStorage.set('[promo].contact-data', this.form.value);
		this.submitting$.next(true);
		this.error$.next(false);

		this.http
			.post(
				this.getMondayEndpoint('pulse'),
				this.encode({
					user_id: '5401371',
					'pulse[name]': 'New Lead' + (this.form.value.phone ? ` (${this.form.value.phone})` : ''),
					group_id: 'new_group'
				}),
				this.httpOptions
			)
			.pipe(flatMap((response: any) => zip(
				this.http
					.put(
						this.getMondayEndpoint('name'),
						this.encode({
							pulse_id: response.pulse.id,
							text: this.form.value.fullName
						}),
						this.httpOptions
					),
				this.http
					.put(
						this.getMondayEndpoint('email'),
						this.encode({
							pulse_id: response.pulse.id,
							text: this.form.value.email
						}),
						this.httpOptions
					)
			)))
			.subscribe(
				() => this.router.navigate(['/gratitude']),
				() => this.error$.next(true),
				() => this.submitting$.next(false)
			);
	}

	private encode(data: Dictionary<string>) {
		return new URLSearchParams(data).toString();
	}

	private getMondayEndpoint(type: 'pulse' | 'name' | 'email') {
		const pathParam = type === 'pulse'
			? 'pulses.json'
			: `columns/${type === 'name' ? 'text6/text.json' : 'text68/text.json'}`;
		return `https://api.monday.com:443/v1/boards/159987023/${pathParam}?api_key=76110e466962369a0c96bb48cb83a6f6`;
	}

}

interface IFeedbackForm {
	fullName: string;
	email: string;
	phone: string;
}
