import { Component, ChangeDetectionStrategy, ElementRef, ViewChild } from '@angular/core';
import { ROUTE_ANIMATIONS, ModalService } from '@bp/shared';

@Component({
	selector: 'bp-root',
	templateUrl: './root.component.html',
	styleUrls: ['./root.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	host: {
		'[class.mat-typography]': 'true'
	},
	animations: [ROUTE_ANIMATIONS]
})
export class RootComponent {
	shouldAnimateOutlet: boolean;
	activeRouteCmpt: { background: string };

	@ViewChild('resizerHost') resizerHost: ElementRef;

	get $resizerHost(): HTMLElement { return this.resizerHost.nativeElement; }

	constructor(public modal: ModalService) { }

}
