import { Component, ChangeDetectionStrategy, ElementRef, Input, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { mutate, AsyncVoidSubject, FADE_IN } from '@bp/shared';
import { RootComponent } from '../root';

@Component({
	selector: 'bp-background',
	templateUrl: './background.component.html',
	styleUrls: ['./background.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	animations: [FADE_IN]
})
export class BackgroundComponent implements OnInit, OnDestroy {
	@Input() name: string;

	private get $host(): HTMLElement { return this.host.nativeElement; }
	private destroyed$ = new AsyncVoidSubject();

	constructor(
		public route: ActivatedRoute,
		private root: RootComponent,
		private host: ElementRef
	) {

	}

	ngOnInit() {
		Observable
			.resize(this.root.$resizerHost)
			.pipe(
				mutate(({ height }) => this.$host.style.height = `${height}px`),
				takeUntil(this.destroyed$)
			)
			.subscribe();
	}

	ngOnDestroy() {
		this.destroyed$.complete();
	}
}
