import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { QuicklinkModule, QuicklinkStrategy } from 'ngx-quicklink';

import { TelemetryService } from '@bp/shared';

import { ModalMenuComponent } from './core';

// app routing module is for lazy routes and modals

const routes: Routes = [
	{
		path: 'product',
		loadChildren: './product#ProductModule'
	},
	{
		path: 'contact',
		loadChildren: './contact#ContactModule'
	},
	{
		path: 'gratitude',
		loadChildren: './gratitude#GratitudeModule'
	},
	{
		path: 'company',
		loadChildren: './company#CompanyModule'
	},
	{
		path: 'careers',
		loadChildren: './careers#CareersModule'
	},
	{
		path: 'legal',
		loadChildren: './legal#LegalModule'
	},
	{
		path: 'menu',
		component: ModalMenuComponent,
		outlet: 'modal'
	},
];

@NgModule({
	imports: [
		QuicklinkModule,
		RouterModule.forRoot(routes, {
			enableTracing: true,
			initialNavigation: 'enabled',
			paramsInheritanceStrategy: 'always',
			onSameUrlNavigation: 'reload',
			scrollPositionRestoration: 'enabled',
			anchorScrolling: 'enabled',
			scrollOffset: [0, 150],
			preloadingStrategy: QuicklinkStrategy,
			errorHandler: TelemetryService.routerErrorHandler
		}),
	],
	exports: [RouterModule, QuicklinkModule],
})
export class AppRoutingModule { }
