import { Component, ChangeDetectionStrategy, HostListener, ElementRef } from '@angular/core';
import { BaseImagesComponent } from '../base-images.component';

@Component({
	selector: 'bp-ai-images',
	templateUrl: './ai-images.component.html',
	styleUrls: ['./ai-images.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class AiImagesComponent extends BaseImagesComponent {

	blues = [
		require('./ai-blue-card-1.png'),
		require('./ai-blue-card-2.png'),
	];

	greens = [
		require('./ai-green-card-1.png'),
		require('./ai-green-card-2.png'),
	];

	active: 'green' | 'blue' = 'green';

	constructor(host: ElementRef) {
		super(host);
	}

	widthMultiplicator = 2;

	@HostListener('click')
	onClick() {
		this.active = this.active === 'green' ? 'blue' : 'green';
	}
}
