import { ViewChild, AfterViewInit, ElementRef, OnDestroy } from '@angular/core';
import { fromEvent, combineLatest } from 'rxjs';
import { throttleTime, takeUntil, observeOn, startWith } from 'rxjs/operators';
import { AsyncVoidSubject, BpScheduler, measure, mutate } from '@bp/shared';

export class BaseImagesComponent implements AfterViewInit, OnDestroy {
	@ViewChild('image') image: ElementRef;
	protected widthMultiplicator = 1;
	private get $image(): HTMLImageElement { return this.image.nativeElement; }
	private get $host(): HTMLElement { return this.host.nativeElement; }
	private destroyed$ = new AsyncVoidSubject();

	constructor(private host: ElementRef) { }

	ngAfterViewInit() {
		combineLatest(
			fromEvent(window, 'resize').pipe(startWith(null)),
			fromEvent(this.$image, 'load')
		)
			.pipe(
				observeOn(BpScheduler.outside),
				throttleTime(16.6),
				measure(() => this.$image.getBoundingClientRect()),
				mutate(({ height, width }) => {
					this.$host.style.height = `${height}px`;
					this.$host.style.width = `${width * this.widthMultiplicator}px`;
				}),
				takeUntil(this.destroyed$),
			)
			.subscribe();
	}

	ngOnDestroy() {
		this.destroyed$.complete();
	}
}
